// src/App.js
import React from "react";
// Вместо BrowserRouter используем HashRouter
import { HashRouter as Router, Routes, Route } from "react-router-dom";

import { AuthProvider } from "./contexts/AuthContext";

// Страница логина
import LoginPage from "./pages/LoginPage";

// Пример: страница админа (с загрузкой таблиц)
import AdminAnalyticsPage from "./pages/AdminAnalyticsPage";

// Пример: страница собственника (только просмотр аналитики)
import OwnerAnalyticsPage from "./pages/OwnerAnalyticsPage";

// Страница "не найдена"
function NotFound() {
  return <div className="text-center mt-4">Страница не найдена (404)</div>;
}

function App() {
  return (
    <AuthProvider>
      {/* ВАЖНО: HashRouter, чтобы не ломалась перезагрузка на GitHub Pages */}
      <Router>
        <Routes>
          {/* Стартовая страница = LoginPage */}
          <Route path="/" element={<LoginPage />} />

          {/* Админ. Например, путь /admin */}
          <Route path="/admin" element={<AdminAnalyticsPage />} />

          {/* Собственник. Например, путь /owner */}
          <Route path="/owner" element={<OwnerAnalyticsPage />} />

          {/* Любые другие пути => 404 */}
          <Route path="*" element={<NotFound />} />
        </Routes>
      </Router>
    </AuthProvider>
  );
}

export default App;
